.game-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100svh;
    font-family: 'Open Sans', sans-serif;
    background-color: #f5f5f5;
}

.game-title {
    font-size: 32px;
    text-decoration: underline;
}

.score-container {
    display: flex;
    width: 80%;
    align-items: center;
    justify-content: space-around;
    border-radius: 10px;
    padding: 10px;
    background-color: #fff;
    margin-bottom: 20px;
    max-width: 580px;
}

.game-board-container {
    border: 2px solid #000;
    border-radius: 10px;
    width: 80%;
    max-width: 720px;
}

.score {
    font-size: 16px;
    font-weight: bold;
}

.timer {
    font-size: 16px;
    font-weight: bold;
}

.player-greeting {
    font-size: 18px;
    margin-bottom: 20px;
    text-align: right;
    margin-right: 20px;
    font-weight: bold;
}

.game-board {
    list-style-type: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding-left: 0px;
}

.game-end-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 20px;
}

.game-end-message {
    font-size: 20px;
    margin-top: 10px;
    margin-bottom: 5px;
}

.play-again-button {
    margin: 10px 0px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #367dc4;
    color: white;
    font-size: 18px;
    cursor: pointer;
    padding: 10px;
}

.game-controls {
    display: flex;
    padding-top: 20px;
    justify-content: flex-end;
    width: 80%;
    max-width: 720px;
}

.restart-button {
    background-color: transparent;
    border: 2px solid lightcoral;
    color: red;
    font-size: 14px;
    cursor: pointer;
    padding: 10px;
    border-radius: 10px;
    font-weight: bold;
}

.restart-button:hover {
    background-color: red;
    color: white;
}

@media screen and (max-width: 576px) {
    .game-title {
        font-size: 24px;
    }
}